export default {
    state: {
        isOpen: false,
        type: '',
        title: '',
        columnId: null,
        groupId: null,
        place: null
    },
    mutations: {
        openDialogPanel(state, payload) {
            state.isOpen = true;
            state.type = payload.type;
            state.columnId = payload.columnId;
            state.groupId = payload.groupId;
            state.place = payload.place;
            state.title = payload.title;
        },
        closeDialogPanel(state) {
            state.isOpen = false;
        }
    },
    actions: {
        openDialogPanel({commit}, payload) {
            commit('openDialogPanel', payload);
        },
        closeDialogPanel({commit}) {
            commit('closeDialogPanel');
        }
    },
    getters: {
        isOpen(state) {
            return state.isOpen
        },
        getType(state) {
            return state.type
        },
        getData(state) {
            if (state.type === 'addTask') {
                return {columnId: state.columnId, groupId: state.groupId, place: state.place}
            } else if (state.type === 'deleteColumn') {
                return {columnId: state.columnId}
            } else if (state.type === 'deleteGroup') {
                return {columnId: state.columnId, groupId: state.groupId, place: state.place}
            } else if (state.type === 'addGroup') {
                return {columnId: state.columnId, groupId: state.groupId, place: state.place}
            } else if (state.type === 'ungroup') {
                return {columnId: state.columnId, groupId: state.groupId}
            } else if (state.type === 'changeTitle') {
                return {columnId: state.columnId, groupId: state.groupId, place: state.place, title: state.title}
            }
        }
    }
}

