<template>
    <div>
        <div class="panel">
            <div class="panel__header">
                <h1 class="title">{{setTitle}}</h1>
            </div>

            <div class="panel__main">
                <div v-if="type==='addTask'">
                    <div class="panel__field">
                        <label class="field__title">Описание задачи:</label>
                        <textarea name="msg" cols="25" rows="3" v-model="text_AddTask"></textarea>
                    </div>
                    <div class="panel__buttons">
                        <button
                                class="button"
                                @click="closeDialogPanel"
                        >
                            Отмена
                        </button>
                        <button
                                class="button"
                                @click="addTask"
                        >
                            Ок
                        </button>
                    </div>
                </div>

                <div v-if="type==='deleteColumn'">
                    <div class="panel__field">
                        <label class="field__title">Вы действительно хотите удалить колонку?</label>
                    </div>
                    <div class="panel__buttons">
                        <button
                                class="button"
                                @click="closeDialogPanel"
                        >
                            Отмена
                        </button>
                        <button
                                class="button"
                                @click="deleteColumn"
                        >
                            Ок
                        </button>
                    </div>
                </div>

                <div v-if="type==='deleteGroup'">
                    <div class="panel__field">
                        <label class="field__title">Вы действительно хотите удалить группу задач?</label>
                    </div>
                    <div class="panel__buttons">
                        <button
                                class="button"
                                @click="closeDialogPanel"
                        >
                            Отмена
                        </button>
                        <button
                                class="button"
                                @click="deleteGroup"
                        >
                            Ок
                        </button>
                    </div>
                </div>

                <div v-if="type==='addGroup'">
                    <div class="panel__field">
                        <label class="field__title">Название группы задач:</label>
                        <input class="field__input" name="msg" v-model="text_AddGroup" autocomplete="off"/>
                    </div>
                    <div class="panel__buttons">
                        <button
                                class="button"
                                @click="closeDialogPanel"
                        >
                            Отмена
                        </button>
                        <button
                                class="button"
                                @click="addGroup"
                        >
                            Ок
                        </button>
                    </div>
                </div>

                <div v-if="type==='ungroup'">
                    <div class="panel__field">
                        <label class="field__title">Вы действительно хотите разгруппировать группу задач?</label>
                    </div>
                    <div class="panel__buttons">
                        <button
                                class="button"
                                @click="closeDialogPanel"
                        >
                            Отмена
                        </button>
                        <button
                                class="button"
                                @click="ungroup"
                        >
                            Ок
                        </button>
                    </div>
                </div>

                <div v-if="type==='changeTitle'">
                    <div class="panel__field">
                        <label class="field__title">Новый заголовок:</label>
                        <input class="field__input" name="msg" v-model="text_changeTitle" autocomplete="off"/>
                    </div>
                    <div class="panel__buttons">
                        <button
                                class="button"
                                @click="closeDialogPanel"
                        >
                            Отмена
                        </button>
                        <button
                                class="button"
                                @click="changeTitle"
                        >
                            Ок
                        </button>
                    </div>
                </div>

            </div>

        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                type: this.$store.getters.getType,
                text_AddTask: '',
                text_AddGroup: '',
                text_changeTitle: this.$store.getters.getData.title
            }
        },
        methods: {
            closeDialogPanel() {
                this.$store.dispatch('closeDialogPanel');
            },
            addTask() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                let place = this.$store.getters.getData.place;
                let text = this.text_AddTask;
                if (text) {
                    this.$store.dispatch('addTask', {columnId, groupId, place, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteColumn() {
                let columnId = this.$store.getters.getData.columnId;
                this.$store.dispatch('deleteColumn', {columnId});
                this.$store.dispatch('closeDialogPanel');
            },
            addGroup() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                let place = this.$store.getters.getData.place;
                let text = this.text_AddGroup;
                if (text) {
                    this.$store.dispatch('addGroup', {columnId, groupId, place, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteGroup() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                this.$store.dispatch('deleteGroup', {columnId, groupId});
                this.$store.dispatch('closeDialogPanel');
            },
            ungroup() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                this.$store.dispatch('ungroup', {columnId, groupId});
                this.$store.dispatch('closeDialogPanel');
            },
            changeTitle() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                let place = this.$store.getters.getData.place;
                let text = this.text_changeTitle;
                if (text) {
                    this.$store.dispatch('changeTitle', {columnId, groupId, place, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            }

        },
        computed: {
            setTitle() {
                if (this.type === 'addTask') return 'Новая задача';
                else if (this.type === 'deleteColumn') return 'Удаление колонки';
                else if (this.type === 'deleteGroup') return 'Удаление группы задач';
                else if (this.type === 'addGroup') return 'Новая группа задач';
                else if (this.type === 'ungroup') return 'Разгруппирование';
                else if (this.type === 'changeTitle') return 'Изменение заголовка';
            }
        }
    }
</script>

<style scoped lang="scss">
    .panel {
        font-family: "Times New Roman";
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
        background-color: #fff;
        .panel__header {
            background-color: #386c62;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            .title {
                color: white;
                font-weight: normal;
                font-size: 25px;
                padding: 0.5em;
                margin: 0;
            }
        }
        .panel__main {
            display: flex;
            flex-direction: column;
            .field__title {
                margin-bottom: 0.5em;
                text-align: center;
                max-width: 20em;
            }
            .panel__field {
                padding: 1em;
                display: flex;
                flex-direction: column;

                textarea {
                    font-size: 20px;
                    padding: 0.3em;
                    resize: none;
                    font-family: "Times New Roman";
                }
                .field__input {
                    margin-bottom: 0.5em;
                    text-align: center;
                    max-width: 20em;
                    font-size: 20px;
                    font-family: "Times New Roman";
                }
            }
            .panel__buttons {
                display: flex;
                justify-content: space-around;
                .button {
                    color: white;
                    outline: none;
                    border: none;
                    background-color: #096e6b;
                    font-size: 20px;
                    font-family: "Times New Roman";
                    margin: 0.5em;
                    padding: 0.3em;
                    width: 7em;
                    cursor: pointer;
                    &:hover {
                        background-color: #0ca88e;
                    }
                }
            }
        }
    }
</style>
