<template>
    <div>
        <div class="group" v-bind:class="{selected: group.selected}">
            <div class="group__header">
                <div></div>
                <span class="group__title"
                      @click="openDialogPanel('changeTitle', columnId, group.id, 'group', group.title)"
                >
                    {{group.title}}
                </span>

                <font-awesome-icon
                        icon="minus"
                        class="group__hide"
                        @click="hideContent(columnId, group.id)"
                />
            </div>

            <div v-if="!group.hide">
                <div class="group__main">
                    <div v-for="task in group.tasks">
                        <div v-if="task.type === 'task'">
                            <Task class="group__task" v-bind:task="task"></Task>
                        </div>
                        <div v-if="task.type === 'group'">
                            <group class="group__task" v-bind:group="task" v-bind:columnId="columnId"></group>
                        </div>
                    </div>
                </div>

                <div class="group__footer">
                    <font-awesome-icon
                            icon="plus"
                            class="btn btn--add"
                            @click="openDialogPanel('addTask', columnId, group.id, 'group')"
                    />
                    <font-awesome-icon
                            icon="trash"
                            class="btn btn--del"
                            @click="openDialogPanel('deleteGroup', columnId, group.id)"
                    />
                    <font-awesome-icon
                            icon="folder-plus"
                            class="btn btn--group"
                            @click="openDialogPanel('addGroup', columnId, group.id, 'group')"
                    />
                    <font-awesome-icon
                            icon="folder-minus"
                            class="btn btn--ungroup"
                            @click="openDialogPanel('ungroup', columnId, group.id, 'group')"
                    />
                </div>
            </div>


        </div>
    </div>
</template>

<script>
    // @ is an alias to /src
    import Column from '@/components/Column.vue'
    import Group from '../components/Group.vue'
    import Task from '../components/Task'

    export default {
        name: 'group',
        components: {
            Column,
            Group,
            Task
        },
        props: ['group', 'columnId'],
        data() {
            return {
                selected: []
            }
        },
        methods: {
            openDialogPanel(method, columnId, groupId, place, title) {
                if (method === 'addTask') {
                    this.$store.dispatch('openDialogPanel', {type: 'addTask', groupId, columnId, place});
                } else if (method === 'deleteGroup') {
                    this.$store.dispatch('openDialogPanel', {type: 'deleteGroup', groupId, columnId, place});
                } else if (method === 'addGroup') {
                    this.$store.dispatch('openDialogPanel', {type: 'addGroup', groupId, columnId, place});
                } else if (method === 'ungroup') {
                    this.$store.dispatch('openDialogPanel', {type: 'ungroup', groupId, columnId});
                } else if (method === 'changeTitle') {
                    this.$store.dispatch('openDialogPanel', {type: 'changeTitle', groupId, columnId, place, title});
                }
            },
            hideContent(columnId, groupId) {
                this.$store.dispatch('hideContent', {columnId, groupId})
            },
            addTask(columnId, groupId) {
                this.$store.dispatch('addTask', {columnId, place: 'group', groupId})
            },
            addGroup(columnId, groupId) {
                this.$store.dispatch('addGroup', {columnId, place: 'group', groupId});
            },
            deleteGroup(columnId, groupId) {
                this.$store.dispatch('deleteGroup', {columnId, groupId});
            },
            ungroup(columnId, groupId) {
                this.$store.dispatch('ungroup', {columnId, groupId})
            }
        },
        computed: {}
    }
</script>

