    .selected {
        background-color: #bdccf1 !important;
    }
    .group {
        color: black;
        border: 1px solid #999997;
        border-radius: 4px;
        background-color: #dce2d6;

        .group__header {
            display: flex;
            justify-content: space-between;
            background-color: #074d42;
            color: white;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
            padding: 0.3em;
            .group__title {
                cursor: pointer;
            }
            .group__hide {
                cursor: pointer;
                margin-right: 3px;
            }
        }

        .group__main {
            text-align: left;
            padding: 0.5em;
            .group__task {
                margin-bottom: 0.5em;
            }
            .task--selected {
                background-color: lightblue;
            }
        }

        .group__footer {
            display: flex;
            justify-content: space-between;
            flex-basis: 100%;

            .btn {
                flex: auto;
                /*width: 2em;*/
                color: #e2efff;
                padding: 0.5em 0.2em;
                font-size: 16px;
                transition: 1s;
                cursor: pointer;
            }
            .btn--add {
                background-color: #075b4d;
                border-bottom-left-radius: 3px;
                &:hover {
                    background-color: #0ca88e;
                }
            }
            .btn--del {
                background-color: #6c1221;
                &:hover {
                    background-color: #c33348;
                }
            }
            .btn--group {
                background-color: #15036c;
                &:hover {
                    background-color: #7b54a9;
                }
            }
            .btn--ungroup {
                background-color: #6c1221;
                border-bottom-right-radius: 3px;
                &:hover {
                    background-color: #c44058;
                }
            }
        }
    }
</style>
